const Hello = r => require(['views/hello'], r);
const Home = r => require(['views/home'], r);
const List = r => require(['views/list'], r);

const Login = r => require(['views/ucenter/login'], r);
const User = r => require(['views/ucenter/index'], r);
const UserHome = r => require(['views/ucenter/userHome'], r);
const Setting = r => require(['views/ucenter/setting'], r);


const NotFound = r => require(['views/notfound'], r);

// 根目录
const rootPath = '';

// 页面路由
const routes = [
  {path: '', redirect: {name: 'home'}},
  {path: '/', component: Home, name: 'home'},
  {path: '/hello', component: Hello, name: 'hello', meta: { requiresAuth: true }},
  {path: '/list', component: List, name: 'list'},
  {path: '/login', component: Login, name: 'login'},
  {path: '/ucenter', component: User, name: 'user',meta: { requiresAuth: true },
		children: [
			{path: 'setting', component: Setting, name: 'setting'},
			{path: '', component: UserHome, name: 'userhome' }
		]	
	}
].map(route => {
  route.path = rootPath + route.path;
  return route;
});

// 404 页
routes.push({path: '*', component: NotFound, name: 'notfound'});

export default routes;
